.container {
    width: 100%;
    height: 100vh;
    text-align: center;
    overflow: hidden;
    position: relative;
}

.container .slider {
    position: absolute;
    width: 200px;
    height: 250px;
    top: 10%;
    left: calc(50% - 100px);
    transform-style: preserve-3d;
    transform: perspective(1000px);
    animation: run 20s linear infinite;
}

@keyframes run {
    from {
        transform: perspective(1000px) rotateX(-12deg) rotateY(0deg) ;
    } to {
        transform: perspective(1000px) rotateX(-12deg) rotateY(360deg) ;
    }
}

.container .slider .item {
    position: absolute;
    inset: 0 0 0 0;
    transform: 
        rotateY(calc( (var(--position) - 1) * (360 / var(--quantity)) * 1deg))
        translateZ(550px);
}

.container .slider .item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.container .content {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: min(1400px, 100vw);
    height: max-content;
    padding-bottom: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.container .content h1 {
    font-size: 16rem;
    line-height: 1em;
    color: #25283B;
    position: relative;
}

.container .content h1:after {
    position: absolute;
    inset: 0 0 0 0;
    z-index: 1;
    -webkit-text-stroke: 2px #ffffff;
    color: transparent;
}

@media (width < 700px) {
    .container .slider {
        width: 100px;
        height: 150px;
    } 
    .container .content h1 {
        font-size: 4rem;
    }  
    .container .slider .item {
        transform: 
            rotateY(calc( (var(--position) - 1) * (360 / var(--quantity)) * 1deg))
            translateZ(250px);   
    }
}